export default `<h1>Como começar a Investir - 5 passos simples.</h1>
<p>Quando o novo investidor se dá conta que existe, além da poupança, uma infinidade de investimentos acaba ficando meio perdido. É normal se perguntar por onde começar e o que significam todas aquelas siglas, como CDB, LCI, SELIC, CDI etc?</p>
<p>O propósito deste artigo é dar um norte para aqueles que desejam iniciar seus investimentos.</p>

<h2>Defina objetivos</h2>
<p>Antes de por a mão no bolso é importante ter em mente quais são os seus objetivos de longo (acima de 5 anos), médio (2 a 5 anos) e curto (menor que 2 anos) prazo.</p>
<p>No curto prazo fica fácil, você irá sempre escolher investimentos conhecidos como investimentos de liquidez diária. Eles permitem que você saque seu dinheiro o mais rápido possível. Nesta classe de ativo é comum encontrar CDB (certificado de depósito bancário) ou o Tesouro SELIC.</p>

<h2>Monte uma reserva de emergência</h2>
<p>O primeiro, e mais importante investimento, que toda pessoa deve ter é a reserva de emergência. Este, como o nome sugere, é um montante para ser utilizado em despesas não previstas,  um exame médico, uma perda de emprego ou até mesmo um acidente automobilístico. É recomendado que o valor da reserva de emergência fique em torno de 4 a 6 vezes a somatória de suas despesas mensais. A quantidade exata depende de sua estabilidade na empresa em que trabalha e da situação em que o país se encontra.</p>

<h2>Saiba qual o seu perfil</h2>
<p>Antes de considerar investir seu dinheiro a médio e longo prazos é necessário conhecer seu perfil de investidor. Você encontrará a resposta para o seu perfil  ao abrir sua conta em uma corretora. Ao responder as questões solicitadas que indicarão seu perfil  de investidor entre conservador, moderado ou agressivo. Esta classificação é muito importante, pois irá auxiliar na construção de sua carteira.</p>
<p>A XP Investimentos conta com sugestão de carteira para cada perfil de investidor. Você pode checar <a href="https://conteudos.xpi.com.br/carteiras-recomendadas/" target="_blank">clicando aqui</a></p>

<h2>Planeje seu aporte mensal</h2>
<p>Se você visa construir um patrimônio para ser utilizado durante sua aposentadoria ou até mesmo para sucessão hereditária é importantíssimo a constância nos aportes. Albert Einstein uma vez disse "Juros compostos são a maior força do Universo.".</p>

<h2>Procure um Assessor de Investimentos</h2>
<p>A melhor maneira de começar investir seu dinheiro é sem dúvida com a ajuda de um Assessor de Investimentos. Ao contrário do que muita gente pensa, o serviço de assessoria de investimento não tem custo algum para o cliente. A própria CVM em sua instrução 497/11 regulamenta em seu artigo 13 que é vedado ao assessor receber de clientes ou em nome de clientes, ou a eles entregar, por qualquer razão e inclusive a título de remuneração pela prestação de quaisquer serviços, numerário, títulos ou valores mobiliários ou outros ativos;</p>
<p>O assessor de investimentos irá lhe explicar as vantagens e desvatagens de cada um dos produtos ofertados pela instituição financeira,  auxiliando durante todo o processo de montagem da carteira e na manutenção do mesmo.</p>
<p>Você pode abrir sua conta na XP Investimentos e ter um assessor de investimentos <a href="https://cadastro.xpi.com.br/desktop/step/1?assessor=A24771">clicando aqui</a> ou entre em contato comigo pelo telefone <a href="tel:43996477425">(43) 99647-7425</a>.
`;
